import { NgModule } from '@angular/core';
import {AnalyticsComponent} from './analytics.component';
import {RouterModule, Routes} from '@angular/router';
import {DateRangePickerComponent} from './components/date_range_picker/date_range_picker.component';
import {CalculatorAnalytics} from './services/calculator-analytics.service';
import {OverviewComponent} from './components/overview/overview.component';
import {UserDetailsComponent} from './components/user_details/user_details.component';
import {TrafficDetailsComponent} from './components/traffic_details/traffic_details.component';
import {UserDetailsPopupComponent} from './components/user_details_popup/user_details_popup.component';
import {SharedModule} from '../../../shared/modules/shared.module';
import {UtilitiesModule} from '../../../shared/modules/utilities.module';

const ANALYTIC_ROUTES: Routes = [
  {
    path: '',
    component: AnalyticsComponent
  }
]

@NgModule({
  declarations: [AnalyticsComponent, OverviewComponent, UserDetailsComponent, TrafficDetailsComponent, UserDetailsPopupComponent],
  imports: [SharedModule, RouterModule.forChild(ANALYTIC_ROUTES), UtilitiesModule
  ],
  exports:[OverviewComponent, UserDetailsComponent, TrafficDetailsComponent, UserDetailsPopupComponent],
  providers: [CalculatorAnalytics],
})
export class AnalyticsModule { }



