import { Component, Input, OnInit, ViewEncapsulation, AfterViewInit, OnDestroy } from '@angular/core';
import { JSONBuilder } from '../../../services/JSONBuilder.service';
import { FormulaService} from  '../../../services/formula.service';

declare var jQuery: any;
declare var math: any;

@Component({
    selector: 'editor-wysiwyg',
    template: require('./assets/html/editor_wysiwyg.component.html'),
    encapsulation: ViewEncapsulation.None,
})

export class EditorWysiwyg implements AfterViewInit, OnInit, OnDestroy {
    control: any;
    @Input() controls: any;
    editorHtml: string;
    currentCount: number;

    constructor(private jsonBuilderHelper: JSONBuilder, private formulaService: FormulaService) {
        this.control = jsonBuilderHelper.getSelectedControl();
    }
    ngOnInit() {
        if (this.controls != undefined)
            this.control = this.controls;
    }

    // getTextOfQuestionNumber(quesNumber: any) {
    //     let currentValue = this.jsonBuilderHelper.getTemplateQuestionare()[quesNumber - 1].props.currentValue;
    //     if (currentValue == '' || currentValue == null || currentValue == undefined)
    //         currentValue = 0;
    //     return currentValue;
    // }

    variableSelected(value: any) {
    }

    ngAfterViewInit() {
        /* wysiwyg editor */
        this.currentCount = 0;

        setTimeout(()=>{
            var options: any = {};
            for (var variable in this.formulaService.allValidVariables())
                options[this.formulaService.allValidVariables()[variable]] = this.formulaService.allValidVariablesWysiywigList()[variable];

            jQuery('.wysiwyg' + this.control._id).on('froalaEditor.buttons.refresh', (e: any, editor: any) => {
                this.control.props.title = e.currentTarget.value;
            });

            jQuery.FroalaEditor.DefineIcon('questions', { NAME: 'input' });
            jQuery.FroalaEditor.RegisterCommand('questions', {
                title: 'Questions list',
                type: 'dropdown',
                focus: true,
                undo: true,
                icon: 'questions',
                refreshAfterCallback: true,
                options: options,
                callback: function (cmd: any, val: any) {
                    this.html.insert(val);
                }
            });

            let editor: any;
            editor = jQuery('.wysiwyg' + this.control._id).froalaEditor({
                heightMax: 250,
                toolbarButtons: ['bold', '|', 'italic', '|', 'underline', '|', 'questions'],
            });

            // jQuery('.fr-wrapper').mCustomScrollbar({
            //     theme: 'dark-3'
            // });

            jQuery('.wysiwyg' + this.control._id).on('froalaEditor.contentChanged', (e: any, editor: any) => {
                this.editorHtml = e.currentTarget.value;
                this.control.props.title = e.currentTarget.value;
            });
        }, 10);
    }

     ngOnDestroy() {
        jQuery('.wysiwyg' + this.control._id).froalaEditor('destroy');
        // jQuery('.fr-wrapper').mCustomScrollbar('destroy');
    }
}



