import { Component, OnInit, ViewEncapsulation, AfterViewInit} from '@angular/core';
import { EDITORS } from './editor';
import { JSONBuilder } from '../../services/JSONBuilder.service';
import {Switch} from '../switch.component';
import {FeatureAuthService} from '../../../../shared/services/feature-access.service';

declare var jQuery: any;
@Component({
    selector: 'editor',
    encapsulation: ViewEncapsulation.None,
    styles: [
        require('./assets/css/bootstrap.colorpickersliders.style.css'),
        require('./assets/css/selectize.default.css'),
        require('./assets/css/editor.custome.css'),
        require('./assets/css/editor.style.css')
    ],
    template: require('./assets/html/editor.component.html'),
})
export class Editor implements OnInit, AfterViewInit {
    title: any;


    constructor(private jsonBuilderHelper: JSONBuilder) {
    }
    ngAfterViewInit() {
        let self = this;
        /*Scroller for right panel and left panel */
        windowScroll();
        jQuery(window).on("resize", function () {
            // jQuery('.side-scroll').mCustomScrollbar('update');
            windowScroll();
        });
        function windowScroll() {
            var rightPanelHeight = jQuery(window).height() - 72;
            jQuery('.sidebar-layout').css('height', rightPanelHeight);
            // jQuery('.side-scroll').mCustomScrollbar({
            //     theme: 'dark-3'
            // });
        }
    }
    ngOnInit() {
        if (this.jsonBuilderHelper.getSelectedModel() === 'Page') {
            this.title = this.jsonBuilderHelper.getSelectedPage().type;
        } else {
            if (this.jsonBuilderHelper.getSelectedModel() === 'Control') {
                this.title = this.jsonBuilderHelper.getSelectedControl().type;
            }
        }
    }

}



